import React, { useState } from 'react'
import { Button, Input, message } from 'antd'
import Icon from '../../../components/light/Icon'
import Api from '../../../api'
import { showLoading, hideLoading } from '../../../utils/tools'

export default function Login(props) {
  const [username, setUsername] = useState('admin')
  const [password, setPassword] = useState('123456')

  const handleEnter = (e) => {
    if (e.keyCode === 13) {
      handleLogin()
    }
  }

  const handleLogin = () => {
    if (username.trim() === '') {
      message.info('用户名不能为空')
      return
    } else if (password.trim() === '') {
      message.info('密码不能为空')
      return
    }
    showLoading()
    Api.light.login({ username, password }).then(res => {
      if (res.code === 200) {
        props.history.push('/light/index/home')
        localStorage.setItem('token', res.data.token)
        hideLoading()
      }
    })
  }

  return (
    <div className="m-login-wrap">
      <div className="m-login">
        <div className="m-login-row">
          <Icon name="username" className="m-login-icon"></Icon>
          <Input
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            placeholder="用户名"
            className="m-login-input"
            autoFocus
          />
        </div>
        <div className="m-login-row">
          <Icon name="password" className="m-login-icon"></Icon>
          <Input
            value={password}
            type="password"
            onChange={(e) => setPassword(e.target.value)}
            onKeyUp={(e) => handleEnter(e)}
            placeholder="密码"
            className="m-login-input"
          />
        </div>
        <div className="m-login-btn-wrap">
          <Button type="primary" onClick={() => handleLogin()}>登录</Button>
        </div>
      </div>
      <div className="m-login-footer">
      Copyright 商之讯管理系统
      </div>
    </div>
  )
}
